import React, {
    useRef,
    useEffect,
    forwardRef,
    useImperativeHandle
} from 'react';
import clsx from 'clsx';
import { MDCSnackbar } from '@material/snackbar';
import { edsWarning } from '@element/react-common';
import { SnackbarDefaultProps, SnackbarPropTypes } from './generated/propTypes';

const Snackbar = forwardRef((props, externalRef) => {
    const {
        actionLabel,
        children,
        className,
        closeOnEscape,
        dismissLabel,
        label,
        leading,
        onClick,
        onClose,
        onClosing,
        onOpen,
        onOpening,
        open,
        stacked,
        timeout,
        ...customProps
    } = props;

    const mdcSnackbar = useRef();
    const ref = useRef();

    useImperativeHandle(externalRef, () => ref.current, []);

    useEffect(() => {
        mdcSnackbar.current = MDCSnackbar.attachTo(ref.current);

        return () => {
            mdcSnackbar.current.destroy();
        };
    }, []);

    useEffect(() => {
        mdcSnackbar.current.listen('MDCSnackbar:closed', onClose);
        return () => {
            mdcSnackbar.current.unlisten('MDCSnackbar:closed', onClose);
        };
    }, [onClose]);

    useEffect(() => {
        mdcSnackbar.current.listen('MDCSnackbar:closed', onClosing);
        return () => {
            mdcSnackbar.current.unlisten('MDCSnackbar:closed', onClosing);
        };
    }, [onClosing]);

    useEffect(() => {
        mdcSnackbar.current.listen('MDCSnackbar:closed', onOpen);
        return () => {
            mdcSnackbar.current.unlisten('MDCSnackbar:closed', onOpen);
        };
    }, [onOpen]);

    useEffect(() => {
        mdcSnackbar.current.listen('MDCSnackbar:closed', onOpening);
        return () => {
            mdcSnackbar.current.unlisten('MDCSnackbar:closed', onOpening);
        };
    }, [onOpening]);

    useEffect(() => {
        if (actionLabel) mdcSnackbar.current.actionButtonText = actionLabel;
    }, [actionLabel]);

    useEffect(() => {
        if (label) mdcSnackbar.current.labelText = label;
    }, [label]);

    useEffect(() => {
        if (open) mdcSnackbar.current.open();
        if (!open) mdcSnackbar.current.close();
    }, [open]);

    useEffect(() => {
        mdcSnackbar.current.closeOnEscape = closeOnEscape;
    }, [closeOnEscape]);

    useEffect(() => {
        if (
            timeout &&
            (timeout === -1 || (timeout >= 4000 && timeout <= 10000))
        ) {
            mdcSnackbar.current.timeoutMs = timeout;
        } else {
            edsWarning(
                'Timeout must be between `4000` and `10000` or set to `-1` to disable.'
            );
        }
    }, [timeout]);

    return (
        <div
            ref={ref}
            className={clsx(
                'lmnt',
                'mdc-snackbar',
                'lmnt-snackbar',
                leading && 'mdc-snackbar--leading',
                stacked && 'mdc-snackbar--stacked',
                className
            )}
            {...customProps}
        >
            <div
                className='mdc-snackbar__surface'
                role='status'
                aria-relevant='additions'
            >
                <div className='mdc-snackbar__label' aria-atomic='false'>
                    {children}
                </div>
                {(actionLabel || dismissLabel) && (
                    <div className='mdc-snackbar__actions' aria-atomic='true'>
                        {actionLabel && (
                            <button
                                tabIndex={0}
                                className='mdc-button mdc-snackbar__action'
                                type='button'
                                onClick={onClick}
                            >
                                <div className='mdc-button__ripple' />
                                <span className='mdc-button__label'>
                                    {actionLabel}
                                </span>
                            </button>
                        )}
                        {dismissLabel && (
                            <button
                                tabIndex={0}
                                className='mdc-button mdc-snackbar__action'
                                type='button'
                            >
                                <div className='mdc-button__ripple' />
                                <span className='mdc-button__label'>
                                    {dismissLabel}
                                </span>
                            </button>
                        )}
                    </div>
                )}
            </div>
        </div>
    );
});

Snackbar.displayName = 'Snackbar';
Snackbar.propTypes = SnackbarPropTypes;
Snackbar.defaultProps = SnackbarDefaultProps;

export default Snackbar;
